.header__container {
  padding-top: 45px;
  display: flex;
  justify-content: space-between; 
}

@media screen and (max-width: 1023px) {
  .header__container {
    padding: 28px 27px 0;
  }
}

@media screen and (max-width: 500px) {
  .header__container {
    flex-direction: column;
  }
}