.popup {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #00000080;
    visibility: hidden;
    opacity: 0;
    transition: visibility .5s, opacity .4s linear;
}